// SOBRESCRIBE
a:visited {color:unset;}
a:-webkit-any-link {color:unset;}
.page {margin:0;}
// /SOBRESCRIBE

// PERSONAL RESET
h1,h2,h3,h4,h5,h6,p {font-size:16px; line-height:16px;}
a {color:unset;}
// PERSONAL RESET

// COLORS
$dark_grey: #333333;
$light_grey: #868686;
// COLORS


// GENERAL STYLES
html {font-size: 16px;}
body {font-family:'Poppins',sans-serif; color:$dark_grey;}
h1 {font-weight:700; margin:0; text-transform:uppercase;}
h2 {color:$light_grey; font-family: 'Raleway', sans-serif; font-size:1rem; font-weight:400; letter-spacing:0.03rem; line-height:1.4rem; margin:2rem 0;}
h3 {font-size:2.2rem; font-weight: 700; line-height:2.4rem;}
h4 {font-size:1.2rem; font-weight: 700;}
p {font-size:1rem; font-weight:400; line-height:1.4rem;}
ul {list-style-type:none; margin:0;}
// /GENERAL STYLES



// MAIN STYLE
.container {display:grid;}


// PRESENTATION left side
.presentation {background-color:#fafafa; position:sticky; top:0; height:100vh; box-sizing:border-box; display:flex; flex-direction:column; align-items:flex-end; padding:5% 5%;}
.presentation__central {display:flex; flex-direction:column; flex:1; justify-content:center; margin-bottom:-10%;}
.presentation__links {align-items: center; display:flex; flex-direction:row; margin-bottom: 20%}
.presentation__email {display:flex; flex-direction:column;}
.presentation__icons {color: $dark_grey; margin-right: 1rem;}
.presentation__icons--item {font-size: 1.1rem; float:left;}
.presentation__icons--item::before {display:inline-block; font-style:normal; font-variant:normal; text-rendering:auto; -webkit-font-smoothing:antialiased;}
// /PRESENTATION left side

// PORTFOLIO right side
.portfolio {display:grid;}

.project-full {display:grid; position:relative;}
.project-full img {align-self:center; justify-self:center;}

.project-half__img {align-self:center; justify-self:center;}

.project__link {width: 100%; height: 100%; z-index: 1; display: block; position: absolute;}
.project__img {transition: all .3s ease-in-out;}
.project__link:hover ~ .project__img {transform: scale(1.05);}

.project__img--contain {object-fit: contain; object-position: center; width: 80%; height: 80%;}
.project__img--cover {height: 100%; object-fit: cover;}
.project__caption--up {position:absolute; top:5%; left:5%; z-index:2;}
.project__kit {bottom:5%; font-size:1rem; display:flex; margin:0; padding:0; opacity:0.3 !important; position:absolute; right:5%;}
.project__kit--item {float:left; margin: 0 .8rem 0 0;}
.project__kit--item i {vertical-align: middle;}
.project__caption--white {color: white;}
.project__caption--black {color: $dark_grey;}
.project__number, .project__company, .project__kit {margin: 0;}
.project__company {font-family: 'Raleway', sans-serif; font-size:.875rem; opacity: .4 !important;}
.project__name {margin: 1.2rem 0 .2rem 0;}
.project__links {margin: 1.2rem 0;}
.project__links--item {display: inline-block; font-size: 1rem; margin-right: .7rem; vertical-align: middle;}
.project__tags {display:flex; flex-flow:row wrap; left:5%; max-width:200px; padding:0; position:absolute; bottom:5%;}
.project__tags--item {align-items:center; background:#0003; border-radius:30px; box-shadow: 0 0 5px 0 rgba(0,0,0,.2); color:white; content:""; display:flex; font-size:.75rem; font-weight:400; height:30px; margin:0 5px 5px 0; min-height:20px; min-width:20px; padding:.5rem .8rem;}
// /PORTFOLIO right side

// /MAIN STYLE



// MEDIA QUERIES
@media (min-width: 576px) {
}
@media (min-width: 768px) {
}
@media (max-width: 991px) { 
    .container {display:grid; grid-template-columns:1fr;}

    .presentation {align-items:center; z-index: unset;}
    .presentation__central {align-items:center;}
    .presentation h1, .presentation h2 {text-align:center;}
    .presentation h1 {font-size:2.4rem; line-height:2.6rem;}
    .presentation h2 {margin:1rem 0 2rem 0;}
    .presentation__icons--item {margin:0 .7rem 0 .7rem;}
    .presentation .button {align-self:center;}

    .project-full{height: 100vh;}
    .split-article {display:grid; grid-template-rows: 1fr 1fr; position:relative;}
    .project-half {display:grid; height:100vh;}

    .project__img--contain {height:90%; object-fit:contain; object-position:center; width:90%;}

    .process__content {align-items:center; display:grid; grid-template-columns:1fr;justify-content: center; margin: 1rem auto;}
    .process__content--description {text-align: center; justify-self:center; padding:5%;}
}
@media (min-width: 992px) { 
    .container {display:grid; grid-template-columns:45% 55%;}

    .presentation {align-items:flex-end; box-shadow: rgba(0, 0, 0, 0.06) 10px 0px 10px 0px; z-index: 1;}
    .presentation__central {align-items:flex-end;}
    .presentation h1, .presentation h2 {text-align:right;}
    .presentation h1 {font-size:2.6rem; line-height:2.8rem;}
    .presentation__icons--item {margin:0 0 0 1.1rem;}

    .project-full{height: 70vh;}
    .split-article {display:grid; grid-template-columns: repeat(2, 1fr); height:50vh; position:relative;}
    .project-half {display:grid; height:50vh;}

    .process__content {align-items:center; display:grid; grid-template-columns:1fr; margin: 4rem auto;}
    .process__content--description {text-align: center; justify-self: flex-end; width: 350px; padding:5%;}

}
@media (min-width: 1200px) {
    .container { display: grid; grid-template-columns: 40% 60%;}
    .process__content {grid-template-columns:1fr 1fr;}
    .process__content--description {text-align: left;}
}
// /MEDIA QUERIES




// SOCIAL ICONS
.fa-dribbble, .fa-behance, .fa-github, .fa-linkedin-in, .fa-codepen {display: inline-block;transition: all .2s ease-out;}
.fa-dribbble:hover, .fa-behance:hover, .fa-github:hover, .fa-linkedin-in:hover, .fa-codepen:hover {transform: translateY(-3px);}
.fa-dribbble:hover {color: #ea4c89;}
.fa-behance:hover {color: #1769ff;}
.fa-github:hover {color: #333;}
.fa-linkedin-in:hover {color: #0077b5;}
.fa-codepen:hover { color: rgb(189, 189, 189); }
// /SOCIAL ICONS








.process {display:grid; grid-template-rows: 100px 230px 2fr; min-height:85vh; padding:10% 5%; justify-content: center; align-items: center;}
.process__title {text-align: center;}
.process__image {margin:0 auto; max-height: 100%;}

.process__content--description p{font-family: 'Raleway', sans-serif; font-size:1rem; line-height:2.2rem;}

.process__toolkit {width: 300px; border-radius: 10px; box-shadow: 0 0 20px rgb(236, 236, 236); position:relative; min-height:400px; justify-self:center;}
.process__toolkit-column {text-align: center; width:300px; padding:1rem;}
.process__toolkit-column h4 {margin:0;}
.process__toolkit-column p {font-size: .7rem; margin-top:0;}
.process__toolkit--items {display:flex; flex-flow:row wrap; justify-content: center; padding:0;}
.process__toolkit--item {align-items:center; background:rgb(236, 236, 236); border-radius:30px; color:rgb(56, 56, 56); content:""; display:flex; font-size:.75rem; font-weight:400; height:30px; margin:2%; min-height:20px; min-width:20px; padding:.5rem .8rem;}
.process__toolkit-column--fixed {border-bottom-left-radius: 10px; border-bottom-right-radius: 10px; width: 100%; min-height: 30px; position: absolute; bottom:0; padding:1rem; background: #ececec;}
.process__toolkit-column--fixed .process__toolkit--item {background:white;}











.footer__inner {align-items:center; background-color:$dark_grey; display:flex; flex-direction: row; justify-content:space-around; height:70px;}
.footer__inner p {font-family: 'Raleway', sans-serif; color:$light-grey; font-size:.875rem; margin:0;}







